'use client'

import { useState, useEffect } from 'react'

interface CryptoPrice {
  bitcoin: number
  gold: number
  sp500: number
}

interface PriceHistory {
  timestamp: number
  bitcoin: number
  gold: number
  sp500: number
}

export function useCryptoPrice() {
  const [currentPrice, setCurrentPrice] = useState<CryptoPrice>({
    bitcoin: 118663,
    gold: 2650,
    sp500: 5800
  })
  const [priceHistory, setPriceHistory] = useState<PriceHistory[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const fetchPrice = async () => {
      try {
        // Fetch real Bitcoin price from CoinGecko API
        const response = await fetch(
          'https://api.coingecko.com/api/v3/simple/price?ids=bitcoin&vs_currencies=usd'
        )
        const data = await response.json()
        
        if (data.bitcoin?.usd) {
          setCurrentPrice(prev => ({
            ...prev,
            bitcoin: data.bitcoin.usd
          }))
        }
      } catch (error) {
        console.error('Failed to fetch Bitcoin price:', error)
        // Keep mock price if API fails
      } finally {
        setLoading(false)
      }
    }

    // Generate mock price history for chart
    const generateMockHistory = () => {
      const history: PriceHistory[] = []
      const now = Date.now()
      const basePrice = currentPrice.bitcoin
      
      for (let i = 30; i >= 0; i--) {
        const timestamp = now - (i * 24 * 60 * 60 * 1000) // 30 days ago
        const variation = (Math.random() - 0.5) * 0.1 // ¬±10% variation
        
        history.push({
          timestamp,
          bitcoin: basePrice * (1 + variation * (i / 30)), // Trending upward
          gold: 2650 * (1 + (Math.random() - 0.5) * 0.02), // Small variations
          sp500: 5800 * (1 + (Math.random() - 0.5) * 0.03) // Small variations
        })
      }
      
      setPriceHistory(history)
    }

    fetchPrice()
    generateMockHistory()

    // Update price every 30 seconds
    const interval = setInterval(fetchPrice, 30000)
    return () => clearInterval(interval)
  }, [])

  return { currentPrice, priceHistory, loading }
}
