'use client'

import { useState, useEffect } from 'react'

interface WalletCardProps {
  name: string
  symbol: string
  icon: string
  color: string
  initialBalance?: number
}

export default function WalletCard({ name, symbol, icon, color, initialBalance = 0 }: WalletCardProps) {
  const [balance, setBalance] = useState(initialBalance)
  const [isEditing, setIsEditing] = useState(false)
  const [tempBalance, setTempBalance] = useState(balance.toString())

  // Load balance from localStorage on mount
  useEffect(() => {
    const savedBalance = localStorage.getItem(`wallet_${symbol}`)
    if (savedBalance) {
      setBalance(parseFloat(savedBalance))
    }
  }, [symbol])

  // Save balance to localStorage when it changes
  useEffect(() => {
    localStorage.setItem(`wallet_${symbol}`, balance.toString())
  }, [balance, symbol])

  const handleEdit = () => {
    setIsEditing(true)
    setTempBalance(balance.toString())
  }

  const handleSave = () => {
    const newBalance = parseFloat(tempBalance) || 0
    setBalance(newBalance)
    setIsEditing(false)
  }

  const handleCancel = () => {
    setTempBalance(balance.toString())
    setIsEditing(false)
  }

  const formatBalance = (amount: number) => {
    return amount.toFixed(8).replace(/\.?0+$/, '')
  }

  const formatUSDValue = (amount: number) => {
    // Mock USD values for display
    const rates: { [key: string]: number } = {
      'BTC': 118663,
      'BCH': 450,
      'ETH': 3800
    }
    const usdValue = amount * (rates[symbol] || 0)
    return `$${usdValue.toFixed(2)}`
  }

  return (
    <div className="bg-white rounded-2xl p-4 shadow-sm border border-gray-100 mb-3">
      <div className="flex items-center justify-between">
        <div className="flex items-center gap-3">
          <div className={`w-12 h-12 ${color} rounded-full flex items-center justify-center`}>
            <span className="text-white font-bold text-xl">{icon}</span>
          </div>
          <div>
            <h3 className="font-semibold text-gray-900">{name}</h3>
            <div className="flex items-center gap-2">
              {isEditing ? (
                <div className="flex items-center gap-2">
                  <input
                    type="number"
                    step="0.00000001"
                    value={tempBalance}
                    onChange={(e) => setTempBalance(e.target.value)}
                    className="w-24 px-2 py-1 text-sm border rounded"
                    autoFocus
                  />
                  <span className="text-sm text-gray-500">{symbol}</span>
                  <button
                    onClick={handleSave}
                    className="text-xs bg-green-500 text-white px-2 py-1 rounded"
                  >
                    ‚úì
                  </button>
                  <button
                    onClick={handleCancel}
                    className="text-xs bg-gray-500 text-white px-2 py-1 rounded"
                  >
                    ‚úï
                  </button>
                </div>
              ) : (
                <button
                  onClick={handleEdit}
                  className="text-sm text-gray-500 hover:text-gray-700 flex items-center gap-1"
                >
                  {formatBalance(balance)} {symbol}
                  <span className="text-xs opacity-50">‚úèÔ∏è</span>
                </button>
              )}
            </div>
          </div>
        </div>
        <div className="text-right">
          <div className="text-lg font-semibold text-gray-900">
            {formatUSDValue(balance)}
          </div>
          <div className="text-sm text-gray-500">
            {balance > 0 ? '+0.00%' : ''}
          </div>
        </div>
      </div>
    </div>
  )
}
