'use client'

import { useState } from 'react'
import { useCryptoPrice } from '@/hooks/useCryptoPrice'

export default function TradePage() {
  const { currentPrice } = useCryptoPrice()
  const [tradeType, setTradeType] = useState<'buy' | 'sell'>('buy')
  const [selectedCrypto, setSelectedCrypto] = useState('BTC')
  const [amount, setAmount] = useState('')
  const [usdAmount, setUsdAmount] = useState('')

  const cryptoOptions = [
    { symbol: 'BTC', name: 'Bitcoin', icon: '‚Çø', color: 'bg-orange-500', price: currentPrice.bitcoin },
    { symbol: 'ETH', name: 'Ethereum', icon: 'Œû', color: 'bg-blue-600', price: 3800 },
    { symbol: 'BCH', name: 'Bitcoin Cash', icon: '‚Çø', color: 'bg-green-500', price: 450 },
  ]

  const selectedCryptoData = cryptoOptions.find(c => c.symbol === selectedCrypto)

  const handleAmountChange = (value: string) => {
    setAmount(value)
    if (selectedCryptoData && value) {
      setUsdAmount((parseFloat(value) * selectedCryptoData.price).toFixed(2))
    } else {
      setUsdAmount('')
    }
  }

  const handleUsdAmountChange = (value: string) => {
    setUsdAmount(value)
    if (selectedCryptoData && value) {
      setAmount((parseFloat(value) / selectedCryptoData.price).toFixed(8))
    } else {
      setAmount('')
    }
  }

  const handleTrade = () => {
    if (!amount || !usdAmount) {
      alert('Please enter an amount to trade')
      return
    }
    
    const action = tradeType === 'buy' ? 'Buy' : 'Sell'
    alert(`${action} Order Placed!\n\n${action} ${amount} ${selectedCrypto}\nValue: $${usdAmount}\n\nThis is a demo transaction - no real trading occurred.`)
  }

  return (
    <div className="p-4 pb-20">
      <div className="flex items-center justify-between mb-6">
        <h1 className="text-2xl font-bold text-gray-900">Trade</h1>
        <div className="text-sm text-gray-500">Demo Mode</div>
      </div>

      {/* Trade Type Selector */}
      <div className="bg-gray-100 rounded-2xl p-1 mb-6 flex">
        <button
          onClick={() => setTradeType('buy')}
          className={`flex-1 py-3 rounded-xl font-semibold transition-colors ${
            tradeType === 'buy'
              ? 'bg-green-500 text-white'
              : 'text-gray-600 hover:text-gray-800'
          }`}
        >
          Buy
        </button>
        <button
          onClick={() => setTradeType('sell')}
          className={`flex-1 py-3 rounded-xl font-semibold transition-colors ${
            tradeType === 'sell'
              ? 'bg-red-500 text-white'
              : 'text-gray-600 hover:text-gray-800'
          }`}
        >
          Sell
        </button>
      </div>

      {/* Cryptocurrency Selector */}
      <div className="bg-white rounded-2xl p-4 shadow-sm border border-gray-100 mb-6">
        <h3 className="font-semibold text-gray-900 mb-3">Select Cryptocurrency</h3>
        <div className="grid grid-cols-3 gap-3">
          {cryptoOptions.map((crypto) => (
            <button
              key={crypto.symbol}
              onClick={() => setSelectedCrypto(crypto.symbol)}
              className={`p-3 rounded-xl border-2 transition-colors ${
                selectedCrypto === crypto.symbol
                  ? 'border-blue-500 bg-blue-50'
                  : 'border-gray-200 hover:border-gray-300'
              }`}
            >
              <div className={`w-8 h-8 ${crypto.color} rounded-full flex items-center justify-center mx-auto mb-2`}>
                <span className="text-white font-bold text-sm">{crypto.icon}</span>
              </div>
              <div className="text-xs font-medium text-gray-900">{crypto.symbol}</div>
              <div className="text-xs text-gray-500">${crypto.price.toLocaleString()}</div>
            </button>
          ))}
        </div>
      </div>

      {/* Amount Input */}
      <div className="bg-white rounded-2xl p-4 shadow-sm border border-gray-100 mb-6">
        <h3 className="font-semibold text-gray-900 mb-4">
          {tradeType === 'buy' ? 'Buy Amount' : 'Sell Amount'}
        </h3>
        
        {/* Crypto Amount */}
        <div className="mb-4">
          <label className="block text-sm text-gray-600 mb-2">
            Amount in {selectedCrypto}
          </label>
          <div className="relative">
            <input
              type="number"
              step="0.00000001"
              value={amount}
              onChange={(e) => handleAmountChange(e.target.value)}
              placeholder="0.00000000"
              className="w-full bg-gray-50 rounded-xl px-4 py-3 pr-16 text-gray-900 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
            <div className="absolute right-4 top-3 text-gray-500 font-medium">
              {selectedCrypto}
            </div>
          </div>
        </div>

        {/* USD Amount */}
        <div className="mb-4">
          <label className="block text-sm text-gray-600 mb-2">Amount in USD</label>
          <div className="relative">
            <input
              type="number"
              step="0.01"
              value={usdAmount}
              onChange={(e) => handleUsdAmountChange(e.target.value)}
              placeholder="0.00"
              className="w-full bg-gray-50 rounded-xl px-4 py-3 pr-16 text-gray-900 focus:outline-none focus:ring-2 focus:ring-blue-500"
            />
            <div className="absolute right-4 top-3 text-gray-500 font-medium">USD</div>
          </div>
        </div>

        {/* Current Price Display */}
        {selectedCryptoData && (
          <div className="bg-gray-50 rounded-xl p-3">
            <div className="flex justify-between items-center">
              <span className="text-sm text-gray-600">Current Price:</span>
              <span className="font-semibold text-gray-900">
                ${selectedCryptoData.price.toLocaleString()}
              </span>
            </div>
          </div>
        )}
      </div>

      {/* Portfolio Balance (Mock) */}
      <div className="bg-white rounded-2xl p-4 shadow-sm border border-gray-100 mb-6">
        <h3 className="font-semibold text-gray-900 mb-3">Available Balance</h3>
        <div className="space-y-2">
          <div className="flex justify-between">
            <span className="text-gray-600">USD Balance:</span>
            <span className="font-semibold">$10,000.00</span>
          </div>
          <div className="flex justify-between">
            <span className="text-gray-600">{selectedCrypto} Balance:</span>
            <span className="font-semibold">0.5 {selectedCrypto}</span>
          </div>
        </div>
      </div>

      {/* Trade Button */}
      <button
        onClick={handleTrade}
        className={`w-full py-4 rounded-2xl font-semibold text-lg text-white transition-colors ${
          tradeType === 'buy'
            ? 'bg-green-500 hover:bg-green-600'
            : 'bg-red-500 hover:bg-red-600'
        }`}
      >
        {tradeType === 'buy' ? 'Buy' : 'Sell'} {selectedCrypto}
      </button>

      {/* Disclaimer */}
      <div className="mt-4 p-4 bg-yellow-50 rounded-xl">
        <p className="text-sm text-yellow-800">
          <strong>Demo Mode:</strong> This is a simulated trading interface. No real transactions will be executed. All balances and trades are for demonstration purposes only.
        </p>
      </div>
    </div>
  )
}
